export function getRandom(max, num) {
  let arr1 = [];
  let arr2 = [];
  // original
  for (let i = 0; i < max; i++) {
    arr1[i] = i + 1;
  }
  arr1.sort(function () {
    return 0.5 - Math.random();
  });
  for (let i = 0; i < num; i++) {
    arr2[i] = arr1[i];
  }
  arr2.sort(function (a, b) {
    return a - b;
  });
  for (let i = 0; i < num; i++) {
    if (arr2[i] < 10) {
      arr2[i] = '0' + arr2[i];
    }
    arr2[i] = arr2[i].toString();
  }
  return arr2;
}
var shakeTimeout;
export function getShark(fn) {
  /* eslint-disable */
  // 
  if (window.DeviceMotionEvent) {
    //device
    window.addEventListener('devicemotion', deviceMotionHandler, false);
  } else {
    console.log('');
  }
  //
  function play() {
    shakeAudio.play();
  }
  //,3000
  var shakeThreshold = 1000;
  //
  var lastUpdate = 0;
  //
  var curShakeX = 0, curShakeY = 0, curShakeZ = 0, lastShakeX = 0, lastShakeY = 0, lastShakeZ = 0;

  function deviceMotionHandler(event) {
    //
    var acceleration = event.accelerationIncludingGravity;
    //
    var curTime = new Date().getTime();
    if ((curTime - lastUpdate) > 100) {
      //
      var diffTime = curTime - lastUpdate;
      lastUpdate = curTime;
      //x
      curShakeX = acceleration.x;
      //y
      curShakeY = acceleration.y;
      //z
      curShakeZ = acceleration.z;
      var speed = Math.abs(curShakeX + curShakeY + curShakeZ - lastShakeX - lastShakeY - lastShakeZ) / diffTime * 10000;
      if (speed > shakeThreshold) {
        //TODO 
        if (shakeTimeout) {
          clearTimeout(shakeTimeout);
        }
        shakeTimeout = setTimeout(function () {
          if (fn) {
            fn()
          }
        }, 1000)
      }
      lastShakeX = curShakeX;
      lastShakeY = curShakeY;
      lastShakeZ = curShakeZ;
    }
  }
  //
  var shakeAudio = new Audio();
  shakeAudio.src = 'static/sound/shake_sound.mp3';
  var shake_options = {
    preload: 'auto'
  }
  for (var key in shake_options) {
    if (shake_options.hasOwnProperty(key) && (key in shakeAudio)) {
      shakeAudio[key] = shake_options[key];
    }
  }

  return {
    play:play
  }
  /* eslint-enable */
}



